import React, { useState, useEffect } from 'react';
import Lottie from 'lottie-react';
import Fireworks from '../Fireworks/Fireworks';
import CustomModal from '../UI/CustomModal/CustomModal';
import TitleCombo from '../UI/TitleCombo/TitleCombo';
import confetti from './confetti.json';
import { useCurrentPhase } from '../../hooks/useCurrentPhase';

import styles from './Phase1Celebration.module.scss';

const Phase1Celebration = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const currentPhase = useCurrentPhase();

  useEffect(() => {
    if (currentPhase === 2 && !localStorage.getItem('Phase1CelebrationPlayer')) {
      localStorage.setItem('Phase1CelebrationPlayer', '1');
      setModalVisible(true);
    }
  }, [currentPhase]);

  return (
    <div
      className={styles.Phase1Celebration}
      onClick={() => {
        setModalVisible(false);
      }}>
      {modalVisible && <Fireworks />}
      <CustomModal
        open={modalVisible}
        mask={false}
        onCancel={() => {
          setModalVisible(false);
        }}>
        <div className={styles.modalContent}>
          <TitleCombo small>
            <h2>Congratulations</h2>
          </TitleCombo>
          <Lottie animationData={confetti} className={styles.animation} />
          <TitleCombo small>
            <h2>Phase 1: 288 Zepoch Nodes</h2>
            <h3>Sold Out in 24 Hours</h3>
          </TitleCombo>
        </div>
      </CustomModal>
    </div>
  );
};

export default Phase1Celebration;
