import React, { useRef, useEffect } from 'react';
import Lottie from 'lottie-react';

import confettiLeft from './confetti-left.json';
import confettiRight from './confetti-right.json';
import fireworks from './fireworks-center.json';
import confettiCelebration from './confetti-celebration.json';

import styles from './Fireworks.module.scss';

const Fireworks = () => {
  const topRef = useRef();
  const leftRef = useRef();
  const centerRef = useRef();
  const rightRef = useRef();

  useEffect(() => {
    centerRef.current.play();
    const sideTimer = setTimeout(() => {
      leftRef.current.play();
      rightRef.current.play();
    }, 1000);
    const topTimer = setTimeout(() => {
      topRef.current.play();
    }, 1500);
    return () => {
      clearTimeout(topTimer);
      clearTimeout(sideTimer);
    };
  }, []);

  return (
    <div className={styles.Fireworks}>
      <Lottie
        lottieRef={topRef}
        animationData={confettiCelebration}
        loop={false}
        className={styles.top}
        autoplay={false}
      />
      <Lottie
        lottieRef={centerRef}
        animationData={fireworks}
        className={styles.center}
        autoplay={false}
      />
      <div className={styles.bottom}>
        <Lottie lottieRef={leftRef} animationData={confettiLeft} autoplay={false} />
        <Lottie lottieRef={rightRef} animationData={confettiRight} autoplay={false} />
      </div>
    </div>
  );
};

export default Fireworks;
